import moment from 'moment'
import {
	initial
} from '../../libs/chineseConversion.js'
export default {
	data() {
		return {
			index:0,
			isSticky: false,
			showReadyAddNotice: false,
			checkedDel: false,
			rid: null,
			rname: null,
			del: null,
			address: null,
			startDate: null,
			endDate: null,
			isCale: false,
			roomLogInfo_len: 0,
			roomLogInfo_len2: 0,
			memberInfo: [],
			roomLogInfo: [],
			adminInfo: [],
			onlineMember: [],
			offlineMember: [],
			adminMember: [],

			modalName: null,
			form_notice: null,

			roomDetailInfo: null,
			wxuserInfo: null,
			userInfo: null,
			currentTab: 0,
			currentNoticeTab: 0,
			tabsList: [{
				name: '日志'
			}, {
				name: '公告'
			}, {
				name: '成员'
			}, {
				name: '文件'
			}, {
				name: '概览'
			}],
			noticeTabsList: [{
				name: '可用公告'
			}, {
				name: '已删除'
			}],
			// 用户索引
			indexList: [],
			indexList_temp: [{
				value: "A",
				index: 0
			}, {
				value: "B",
				index: 0
			}, {
				value: "C",
				index: 0
			}, {
				value: "D",
				index: 0
			}, {
				value: "E",
				index: 0
			}, {
				value: "F",
				index: 0
			}, {
				value: "G",
				index: 0
			}, {
				value: "H",
				index: 0
			}, {
				value: "I",
				index: 0
			}, {
				value: "J",
				index: 0
			}, {
				value: "K",
				index: 0
			}, {
				value: "L",
				index: 0
			}, {
				value: "M",
				index: 0
			}, {
				value: "N",
				index: 0
			}, {
				value: "O",
				index: 0
			}, {
				value: "P",
				index: 0
			}, {
				value: "Q",
				index: 0
			}, {
				value: "R",
				index: 0
			}, {
				value: "S",
				index: 0
			}, {
				value: "T",
				index: 0
			}, {
				value: "U",
				index: 0
			}, {
				value: "V",
				index: 0
			}, {
				value: "W",
				index: 0
			}, {
				value: "X",
				index: 0
			}, {
				value: "Y",
				index: 0
			}, {
				value: "Z",
				index: 0
			}],
			userList: [],
			scrollTop: 0,
			currentAdminTab: 0,
			adminTabList: [{
				name: '添加管理员'
			}, {
				name: '删除管理员'
			}]
		}
	},
	onPageScroll(e) {
		this.scrollTop = e.scrollTop;
	},
	onLoad: function(option) {
		var that = this
		console.log(option)
		that.rid = parseInt(option.rid)
		that.del = parseInt(option.del)
		that.rname = option.rname
		that.address = option.address

		that.wxuserInfo = uni.getStorageSync("wxuserInfo")
		global.getUserInfo()
		that.userInfo = uni.getStorageSync("userInfo")

		var e = {}
		e.startDate = null
		e.endDate = null
		that.getRoomDetail(e)

	},
	watch: {
		checkedDel(newValue, oldValue) {
			console.log("new" + newValue)
			console.log("old" + oldValue)
			this.changeRoomStatus(newValue, oldValue)
		}
	},
	methods: {
		adminTabsChange(index) {
			var that = this
			that.showChangeAdmin(index)
		},


		// 添加管理员			
		changeAdmin(index) {
			console.log(index)
			var that = this				
			var user = index
			var method = that.currentAdminTab == 0 ? 1 : 0
			var title = ''
			// 添加用户
			if (method == 1) {
				title = "确定将" + user.realName + "设为" + that.rname + "的管理员?"
			} else {
				title = "确定删除" + that.rname + "的管理员" + user.realName + "?"
			}
			uni.showModal({
				title: title,
				success: function(res) {
					if (res.confirm) {
						console.log("确定了")
						uni.request({
							url: "https://api.attendance.lifeisgg.online/MP/admin/addOrDelAdmin",
							data: {
								rid: that.rid,
								uid: user.uid,
								method: method
							},
							success(res) {
								console.log(res)
								var e = {}
								e.startDate = null
								e.endDate = null
								e.refresh = true
								that.getRoomDetail(e)
								
								setTimeout(function () {									
								if (res.data.code == 200) {
									that.showChangeAdmin(0)	
									that.showToast("添加管理员成功", "success", "bottom")
								} else if (res.data.code == 201) {
									that.showChangeAdmin(1)	
									that.showToast("删除管理员成功", "success", "bottom")
								}
								}, 300);				
								

							}
						})
					}
				}
			})
		},
		// 添加删除管理员时的获取用户列表
		showChangeAdmin(index) {
			var that = this
			console.log(index)
			that.modalName = "showChangeAdmin"
			that.currentAdminTab = index
			uni.showLoading({
				title: '正在加载用户'
			})
			// 初始化索引
			for (let item of that.indexList_temp) {
				item.index = 0
			}

			// 添加用户
			if (that.currentAdminTab == 0) {
				uni.request({
					url: "https://api.attendance.lifeisgg.online/MP/admin/getAllUser",
					data: {},
					success(res) {
						console.log(res)
						that.userList = []
						var userList = []
						userList = res.data.userList
						console.log("userList:::"+userList)
						
						if (that.adminMember.length != 0) {
							for (let item of userList) {
								for (let item1 of that.adminMember) {
									if (item.uid == item1.uid) {
										item.uid = null
									}
								}
							}
															
						}
						
						for(let item of userList){
							if(item.uid != null){
								that.userList.push(item)
							}
						}
						
						
						console.log(that.userList)

						that.indexList = []
						for (let item of that.userList) {
							item.index = initial(item.user.realName)
							for (let item2 of that.indexList_temp) {
								if (item2.value == item.index) {
									console.log("符合条件")
									if(item2.index != 1){
										item2.index = 1
										that.indexList.push(item2.value)
									}																				
								}
							}
						}
						console.log(that.indexList)
					}
				})
			}
			// 删除用户
			else if (that.currentAdminTab == 1) {
				var e = {}
				e.startDate = null
				e.endDate = null
				e.refresh = true
				that.getRoomDetail(e)
				that.indexList = []

			}
			uni.hideLoading()

		},
		showAdmin() {
			this.currentTab = 2
		},
		// 改变房间状态
		changeRoomStatus(newValue, oldValue) {
			var that = this
			var status = 0
			if (newValue == true) {
				status = 1
			}
			if (that.del == status) {
				return false
			} else {
				var title = "关闭"
				if (status == 1) {
					title = "开放"
				}
				uni.showModal({
					title: "您是否要" + title + that.rname,
					success: function(res) {
						if (res.cancel) {
							that.checkedDel = oldValue
							return false
						}
						if (res.confirm) {
							uni.request({
								url: "https://api.attendance.lifeisgg.online/MP/admin/changeRoomStatus",
								data: {
									uid: that.userInfo.uid,
									rid: that.rid,
									del: status
								},
								success(res) {
									console.log(res)
									if (res.data.code == 200) {
										that.showToast("实验室开放成功", "success", "bottom")
										that.checkedDel == newValue
										that.del = 1
									} else if (res.data.code == 201) {
										that.showToast("请确保实验室没有人员后再关闭", "warning", "bottom")
										that.checkedDel == oldValue
									} else if (res.data.code == 202) {
										that.showToast("实验室关闭成功", "success", "bottom")
										that.checkedDel == newValue
										that.del = 0
									}
									var e = {}
									e.startDate = null
									e.endDate = null
									e.refresh = true
									that.getRoomDetail(e)
								}
							})
						}
					},
				})
			}
		},
		/* 实验室界面tabs栏 */
		noticeTabsChange(index) {
			var that = this
			that.currentNoticeTab = index
		},
		// 打开日期筛选
		showCale() {
			this.isCale = true
		},
		// tabs栏切换
		tabsChange(index) {

			this.currentTab = index;
		},
		// 监听
		sectionChange(index) {
			this.currentTab = index;
		},
		// toast提示
		showToast(title, type, position) {
			this.$refs.uToast.show({
				title: title,
				type: type,
				position: position,
				duration: 2500
			})
		},
		deleteNotice(e) {
			var that = this
			var aid = e.currentTarget.dataset.aid
			console.log("aid=" + aid)
			console.log("that.userInfo.uid=" + that.userInfo.uid)
			console.log("that.rid=" + that.rid)
			uni.showModal({
				title: "确定要删除这条公告吗",
				success: function(res) {
					if (res.confirm) {
						uni.request({
							url: "https://api.attendance.lifeisgg.online/MP/admin/addOrDeleteNotice",
							data: {
								aid: aid,
								uid: that.userInfo.uid,
								rid: that.rid,
								method: 0
							},
							success(res) {
								console.log(res)
								var e = {}
								e.startDate = null
								e.endDate = null
								e.refresh = true
								that.getRoomDetail(e)
								that.showToast("删除公告成功", "success", "bottom")
							}
						})
					}
				},
			})
		},


		readyAddNotice() {
			var that = this
			that.showReadyAddNotice = true
			/* uni.navigateTo({
				url:'addNotice?rid='+that.rid+'&uid='+ that.userInfo.uid+'&rname='+that.rname
			}) */
		},
		hideModal() {
			this.modalName = null
		},
		// 添加公告
		addNotice() {
			var that = this
			uni.request({
				url: "https://api.attendance.lifeisgg.online/MP/admin/addOrDeleteNotice",
				data: {
					rid: that.rid,
					uid: that.userInfo.uid,
					notice: that.form_notice,
					method: 1
				},
				success(res) {
					console.log(res)
					that.form_notice = ""
					var e = {}
					e.startDate = null
					e.endDate = null
					e.refresh = true
					that.getRoomDetail(e)
					that.showReadyAddNotice = false
					that.showToast("添加公告成功", "success", "bottom")
				}
			})
		},


		// 获取房间信息
		getRoomDetail(e) {
			uni.showLoading({
				title: '正在加载实验室信息'
			})
			var that = this
			var startDate = e.startDate
			var endDate = e.endDate
			var refresh = e.refresh
			uni.request({
				url: "https://api.attendance.lifeisgg.online/MP/admin/getRoomDetail",
				data: {
					rid: that.rid
				},
				success(res) {
					console.log(res)
					that.memberInfo = res.data.memberInfo
					that.roomLogInfo = res.data.roomLogInfo
					that.adminInfo = res.data.adminInfo
					if (that.del == 1) {
						that.checkedDel = true
					}

					that.onlineMember = []
					that.offlineMember = []
					that.adminMember = []
					// 分类管理员,在线人员,非在线人员
					for (let i = 0; i < that.memberInfo.length; i++) {
						if (that.memberInfo[i].isAdmin == 1) {
							that.adminMember.push(that.memberInfo[i])
						}
						if (that.memberInfo[i].status == 1) {
							that.onlineMember.push(that.memberInfo[i])
						} else if (that.memberInfo[i].status == 0) {
							that.offlineMember.push(that.memberInfo[i])
						}
					}

					// 日期筛选
					if (startDate != null && endDate != null) {
						console.log("选择日期模式")
						var roomLogInfo_2 = []
						for (let item of that.roomLogInfo) {
							let ldate = moment(item.ldate).format('YYYY-MM-DD')
							if (moment(ldate).isBefore(startDate) == false && moment(ldate).isAfter(endDate) == false) {
								console.log("符合条件" + item)
								roomLogInfo_2.push(item)
							}
						}
						that.roomLogInfo = roomLogInfo_2
						that.roomLogInfo_len2 = that.roomLogInfo.length
						that.startDate = startDate
						that.endDate = endDate
						if (refresh != true) {
							that.showToast("获取从" + startDate + "到" + endDate + "的日志成功", "success", "bottom")
						}

					} else {
						that.roomLogInfo_len = that.roomLogInfo.length
						that.roomLogInfo_len2 = that.roomLogInfo.length
						if (that.roomLogInfo[0] != null) {
							that.startDate = moment(that.roomLogInfo[that.roomLogInfo.length - 1].ldate).format('YYYY-MM-DD')
							that.endDate = moment(that.roomLogInfo[0].ldate).format('YYYY-MM-DD')
						}
						if (refresh != true) {
							that.showToast("实验室" + that.rname + "详细信息查询成功", "success", "bottom")
						}

					}
					console.log("startDate" + startDate)
					console.log("endDate" + endDate)
					uni.hideLoading()
					that.isCale = false
				}
			})
		},
		// 查找所有
		getRoomDetail2() {
			var that = this
			var e = {}
			e.startDate = null
			e.endDate = null
			that.getRoomDetail(e)
		}


	}
}
